// Single language translation field label/button
.ft-translate-button-container {
  margin-top: 1rem;

  .ft-icon {
    color: rgb(135, 135, 135);
    display: inline-block;
    margin-right: 0.3rem;
  }

  .ft-translation-available,
  .ft-translate-button {
    cursor: default;
    font: {
      weight: normal;
      size: 0.9rem;
    }
  }

  .ft-translation-status {
    color: rgb(135, 135, 135);
    letter-spacing: 0.01rem;
    font: {
      style: italic;
    }
  }

  .ft-translate-button {
    background: none;
    border: none;
    color: var(--ft-secondary);
    cursor: pointer;
    transition: color 0.3s;

    &:hover {
      color: var(--ft-secondary-dark);
    }
  }
}
