/* Click to copy */
.ft-click-to-copy {
  position: relative;

  &:before {
    align-items: center;
    background-color: rgba(62, 185, 152, 0.7);
    color: rgb(255, 255, 255);
    content: 'Click to copy';
    cursor: pointer;
    display: flex;
    font-size: 18px;
    font-weight: 800;
    height: 100%;
    justify-content: center;
    opacity: 0;
    position: absolute;
    transition: opacity 0.5s, background-color 0.3s;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 5;
  }

  &:hover:before {
    opacity: 1;
  }

  &.copied:before {
    background-color: rgba(62, 185, 152, 0.9);
    content: 'Copied!';
  }
}

/* Errors */
.fluency-admin-view .InputfieldHeader,
.fluency-admin-view .InputfieldContent {
  transition: color 0.3s, background 0.3s, background-color 0.3s;
}
