// Classes are defined in FtLanguageTab.js
// Indicates that the content of an associated field has changed since page load
$default-state-border: 2px solid var(--ft-white);
$modified-state-border: 2px solid var(--ft-primary-light);

*[data-ft-initialized] {
  .langTabs {
    li {
      a {
        border-top: $default-state-border;
        transition: border-top 0.3s;

        &.ft-content-changed {
          font-style: italic;
          border-top: $modified-state-border;
        }
      }
    }
  }
}

#ProcessPageEditSettings *[data-ft-initialized] {
  .LanguageSupport {
    border-left: $default-state-border;
    padding-left: 1rem;
    transition: border-left 0.3s;

    &.ft-content-changed {
      border-left: $modified-state-border;
    }
  }
}
